@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;0,600;0,700;0,800;0,900;1,600;1,700&display=swap');

*{
  font-family: 'Nunito', sans-serif;
}

.App {
  text-align: center;
}

.profile{
  background: #262A41 0% 0% no-repeat padding-box;
  opacity: 1;
}

.form_search{
  margin: 3rem 0 3rem 0;
}

button.form_button.btn.btn-outline-success {
  background: #fff 0% 0% no-repeat ;
  color: #262A41;
  border: 1px solid #3C3F54;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;

}

input.mr-sm-2.search_bar.form-control{
  width: 70% !important;
  background: #3C3F54 0% 0% no-repeat ;
  border: 1px solid #3C3F54;
  border-top-left-radius: 28px;
  border-bottom-left-radius: 28px;
  opacity: 1;
  padding: 1rem;
  color: #fff;

}

input.mr-sm-2.search_bar.form-control::placeholder{
  letter-spacing: -0.02px;
  color: #FFFFFF;
  opacity: 0.7;
}

p.details {
  color: white;
  font-size: 18px;
}

.category{
  margin-top: 4rem;
}

.category h6{
  color: #fff;
  font-size: 22px;
}

.icons{
  background: #7946C1 0% 0% no-repeat padding-box;
  border-radius: 18px;
  opacity: 1;
  color: #fff;
  font-size: 20px;
  margin: 10px;
  text-align: center;
  border: none;
}

.row .pad1y{
  font-size: 18px;
}

.icons svg{
  width: 23px;
  height: 51px;
}

.col-lg-8.results {
  margin-top: 3rem;
}

.col-lg-4.user {
  border-radius: 20px;
  margin-bottom: 2.1rem;
  overflow: hidden;
  box-shadow: 10px 10px 40px #0000000D;
}

p.email {
  font-size: 16px;
}

svg#profile_email{
  font-size: 25px;
}

svg.profile_icons {
  font-size: 30px;
}

.dropdown {
  background: #7946C1 0% 0% no-repeat padding-box;
  border-radius: 18px;
  width: 100%;
}

button#dropdown-basic{
  background: #7946C1 0% 0% no-repeat padding-box;
  border: none;
  outline: none;
  cursor: pointer;
  width: 80%;
  border-radius: 0;
}

.btn-primary.focus, .btn-primary:focus {

  box-shadow: 0 0 0 !important;
}

a.profile_url{
  color: #00d;
  overflow: hidden;
}

.middle{
  position: relative;
  /* top: 50%; */
}

.list-group-item {
  padding: .75rem 1rem !important;
}

.front, .back{
  width: 100%;
  height: 100%;
  overflow: hidden;
  backface-visibility: hidden;
  position: relative;
  transition: transform .6s linear;
}

.back_content{
  background: #fff 0% 0% no-repeat ;
  height: 100%;
  position: absolute;
  top: 0%;
  width: 90%;
}

@media(max-width: 768px){
  .back_content {
    width: 92% !important;
}
}

.front.back{
  transform: perspective( 600px) rotateY(0deg);
}

.middle_content{
  top: 0;
}

.back{
  transform: perspective( 600px) rotateY(180deg);
}

.page_card:hover > .front{
  transform: perspective( 600px) rotateY(-180deg);

}

.page_card:hover > .back{
  transform: perspective( 600px) rotateY(0deg);
}

.page-link {
  color: #000 !important;
}

select.form-control, select.form-control:focus {
  color: #fff;
  background-color: transparent;
  border: none;
  outline: none;
}

 select.form-control:focus{
   color: #262A41;
 }
